import { useEffect, useState } from "react";

// Comp Auto slider
import ImageSlider from "./Comp-img-slider/Img-slider";

// Popup

// Animate on scroll Library
import AOS from "aos";
import "aos/dist/aos.css";

export default function BannerCta(Props) {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  useEffect(()=> {
    comingPopup ? document.body.style.overflow="hidden" : document.body.style.overflow="auto"
  })

  const [comingPopup, setComingPopup] = useState(false)

  const HandleShow = () => {
    setComingPopup(!setComingPopup)
  }

  return (
    <>
    {comingPopup && (
        <div className="Comingsoon-continer">
          <div className="black-background" onClick={HandleShow} />
            <div className="main-comingsoon">
              <div style={{marginBottom: "16px"}}>
                <img src="/image/imgComp/comingnew.png" alt="" width={"100px"}/>
              </div>
              <div style={{textAlign: "center"}}>
                  <h4 style={{fontFamily: "inter", fontSize: "14px", marginBottom : "2px"}}>Mobile version will avaliable</h4>
                  <p style={{fontFamily: "inter", fontSize: "12px", opacity: "80%"}}>Stay tune, mate!</p>
              </div>
              <div style={{marginTop: '16px'}}>
                <button className="button-coming" onClick={HandleShow}>okay</button>
              </div>
          </div>
        </div>
    )}

      <div className="Banner-cta" data-aos-delay="1050">
        <div className="Wrapper-banner-cta">
          {/* Bagian Txt n Btn */}
          <div className="Heading-top-Banner">
            {/* Heading text */}
            <div>
              <h1 style={{color: Props.DarkMode ? "white" : "black"}}>Pasang aplikasi dihp mu </h1>
              <p style={{color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>
                Pasang versi aplikasi dihp kamu biar bisa diakses secara
                offline!
              </p>
            </div>

            {/* Button section */}
            <div>
              <button onClick={() => setComingPopup(true)}>
                Install <img src="/Icons/Vector.svg" alt="Install ic" />
              </button>
            </div>
          </div>
          {/* Bagian Banner */}
          <div className="Banner-home-cta">
            <ImageSlider />
          </div>
        </div>
      </div>
    </>
  );
}