import { useContext, useEffect, useRef, useState } from "react";
import BottomNav from "../../Nav-comp/Bottom-nav/Bottom-nav";
import Header from "../../Nav-comp/Top-nav/Header";
import ImageSlider from "../../Board-Home/Comp-img-slider/Img-slider";

import LoadingPage from "../../Loading-page/Loadcomp";

// Darkmode Context
import { DarkModeContext } from "../../../DarkMode/Darkmode";
import RecipeScroll from "../../Recipe-scroll/RecipeScroll";

export default function ExploreComp(Props) {
  useEffect(() => {
    window.scrollTo({
      top: "0px",
    });
  }, []);

  const [loadPage, setLoadPage] = useState(false)
  useEffect(()=> {
    setTimeout(()=> {
      setLoadPage(true)
    }, 300)
  })

  // Darkmode Toggle
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  return (
    <>
      {modeToggle ? (
        <div className="Explore-comp" style={{backgroundColor: "black"}}>
          <header>
            <div className="nav-top" style={{backgroundColor: "black"}}>
              <p>Explore</p>
              <div>
                <Header 
                  DarkMode={modeToggle}
                  NewWidth={{width: '100%'}}
                  NewHeight={{ height: "fit-content" }}
                  HiddenSetting={{ display: "none" }}
                  NewSearchBox={{ width: "100%" }}
                />
              </div>
            </div>
          </header>
          {/* Content to load */}
          {loadPage ? (
            <>
              <div className="Image-slider">
                <ImageSlider NewMargin={{margin: "0px 0px"}} HideIndicator={{indicators: false}} />
              </div>
              <RecipeScroll DisableViewMore={{display: "none"}} PropsHeight={{height: "fit-content"}}/>
              <RecipeScroll DisableViewMore={{display: "none"}} PropsHeight={{height: "fit-content"}} />
            </>
          
            ) : (
              <LoadingPage DarkMode={modeToggle} />
            )}

            {modeToggle ? (
              <BottomNav DarkMode={modeToggle} />
            ) : (
              <BottomNav />
            )}
        </div>
      ) : (
        <div className="Explore-comp" style={{backgroundColor: "var(--Ground-White)"}}>
          <header>
            <div className="nav-top">
              <p>Explore</p>
              <div>
                <Header 
                  NewWidth={{width: '100%'}}
                  NewHeight={{ height: "fit-content" }}
                  HiddenSetting={{ display: "none" }}
                  NewSearchBox={{ width: "100%" }}
                />
              </div>
            </div>
          </header>
          {/* Content to load */}
          {loadPage ? (
            <>
              <div className="Image-slider">
                <ImageSlider NewMargin={{margin: "0px 0px"}} HideIndicator={{indicators: false}} />
              </div>
              <RecipeScroll DisableViewMore={{display: "none"}} PropsHeight={{height: "fit-content"}}/>
              <RecipeScroll DisableViewMore={{display: "none"}} PropsHeight={{height: "fit-content"}} />
            </>
          
            ) : (
              <LoadingPage DarkMode={modeToggle} />
            )}

            {modeToggle ? (
              <BottomNav DarkMode={modeToggle} />
            ) : (
              <BottomNav />
            )}
        </div>
      )}
    
    
    </>
    
  );
}
