import BottomNav from "../Nav-comp/Bottom-nav/Bottom-nav"
import { useContext } from "react"

// Darkmode Context
import { DarkModeContext } from "../../DarkMode/Darkmode"

export default function Error404() {
    // Darkmode Toggle
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)
    return (
        <>
            <style>
                {`
                    .error-container {
                    width: 100%;
                    height: 100vh;
                    
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    gap: 32px;

                    }
                
                `}
            </style>
            {modeToggle ? (
                <>
                    <div className="error-container" style={{backgroundColor: "black"}}>
                        <img src="/Image/imgComp/404orange.png" alt="" width={"220px"} />
                        <div style={{textAlign: "center"}}>
                            <p style={{fontFamily: "Main-Bold", color: modeToggle ? "white" : "black"}}>Page not found!</p>
                            <p style={{fontFamily: "inter", fontSize: '12px', fontWeight: "400", color: modeToggle ? "var(--Inner-Input)" : ""}}>Turn back into previous pages</p>
        
                        </div>
                    </div>
                    {modeToggle ? (
                        <BottomNav DarkMode={modeToggle} />
                    ) : (
                        <BottomNav />
                    )}
                </>
            ) : (
                <>
                    <div className="error-container">
                            <img src="/Image/imgComp/404orange.png" alt="" width={"220px"} />
                            <div style={{textAlign: "center"}}>
                                <p style={{fontFamily: "Main-Bold"}}>Page not found!</p>
                                <p style={{fontFamily: "inter", fontSize: '12px', fontWeight: "400"}}>Turn back into previous pages</p>
            
                            </div>
                        </div>
                        {modeToggle ? (
                            <BottomNav DarkMode={modeToggle} />
                        ) : (
                            <BottomNav />
                        )}
                </>
            )}
        </>
    )
}