import { useEffect, useContext } from "react"
import ContentHomeCategory from "./ContentHomeCategory"
import AOS from "aos"

// DarkMode
import { DarkModeContext } from "../../../DarkMode/Darkmode"

export default function HomeCategory() {
    useEffect(()=> {
        AOS.init({duration: 1000})
    })

    // Darkmode
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)

    return (
        // data-aos="slide-up"
        <div data-aos-delay="1050">
        <style>
            {`
                :root {
                    --Gray-backColor: #E6E6E6;
                }

                .homeCategory {
                    margin-top: 32px;
                    min-width: 320px;
                }

                .Wrapper-homeC {
                    height: 100%;
                    width: 100%;
                    padding: 0px 16px;

                    display: flex;
                    justify-content: center;
                    flex-direction: row;
                    flex-wrap: wrap;
                    gap: 8px;
                }

                .Wrapper-homeC div {
                    width: 100px;  
                    height: 50px;
                    border-radius: 8px;
                    color: ${modeToggle ? "white" : "black"};
                    background-color: ${modeToggle ? "rgb(41, 41, 41)" : "var(--Gray-backColor)"};
                }
            `}
        </style>
        <div className="homeCategory">
            <div className="Wrapper-homeC">
                <div>
                    <ContentHomeCategory TitleCategory="Protein"/>
                </div>
                <div>
                    <ContentHomeCategory TitleCategory="Cookie & pie"/>
                </div>
                <div>
                    <ContentHomeCategory TitleCategory="Vegetarian"/>
                </div>
                <div>
                    <ContentHomeCategory TitleCategory="Vegetarian"/>
                </div>
                <div>
                    <ContentHomeCategory TitleCategory="Vegetarian"/>
                </div>
                <div>
                    <ContentHomeCategory TitleCategory="Vegetarian"/>
                </div>
            </div>    
        </div>    
    </div>
    )
}