import { useContext } from "react";
import { SavedContext } from "./Newest-recipe-sect/ContextProps";
import BottomNav from "./Nav-comp/Bottom-nav/Bottom-nav";

// Darkmode Context
import { DarkModeContext } from "../DarkMode/Darkmode";

export default function SavedNav() {
  const {solid, setSolidSave} = useContext(SavedContext);

  // Darkmode Toggle
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  return (
    <>
        <div>
        {solid && <p>berhasil</p>}
        </div>
        {modeToggle ? (
          <BottomNav DarkMode={modeToggle} />
        ) : (
          <BottomNav />
        )}
    </>
  );
}
