export default function LoadingPage(Props) {
  // Darkmode
  return (
    <>
      <style>
        {`
            /* Loader style */
            .Load-container {
            width: 100%;
            height: 80vh;
            background-color: ${Props.DarkMode ? "black" : "var(--Ground-White)"};
            color: ${Props.DarkMode ? "white" : "black"};
            display: flex;
            justify-content: center;
            align-items: center;
            }

        `}
      </style>

      <div className="Load-container">
          <p style={{fontWeight: "400", fontFamily: "inter", fontSize: '12px'}}>Loading...</p>
      </div>
    </>
  );
}
