import { BrowserRouter, Routes, Route } from 'react-router-dom';
import './App.css';

import './Component/Board-Home/Banner-cta.css';
import './Component/Newest-recipe-sect/New-recipe-section.css';
import './Component/Nav-comp/Bottom-nav/Bottom-nav.css';
import './Component/Main-cta-home/Cta-home-main.css';
import './Component/Resep-pilihan/ResepPilihan.css';

import './Component/1.Btm-nav-comp/Explore/Explore-comp.css';

import Home from './Component/Home';
import Setting from './Component/Nav-comp/Setting-sect/Setting';
import ExploreComp from './Component/1.Btm-nav-comp/Explore/Explore-comp';
import SavedNav from './Component/SavedSect'
import Error404 from './Component/Error404/Error404';

// Context Feedback
import { SavedProvider } from './Component/Newest-recipe-sect/ContextProps';

// Context Darkmode
import DarkModeProvider from './DarkMode/Darkmode';
// import { DarkModeContext } from './DarkMode/Darkmode';
import { useEffect, useState} from 'react';

// !Offline Indicator
import OfflineIndicator from './Popup-Component/Offline-indicator/Offline-indicator';

// Feedback
import FeedbackProvider from './Popup-Component/Feedback-Popup/FeedbackContext';

export default function App() {
  window.addEventListener('scroll', () => {
    console.log(window.scrollY);
  });

  // !Online offline Indicator
  const [WebOnline, setWebOnline] = useState(navigator.onLine)

  function HandleConnectionOnline() {
      setWebOnline(navigator.onLine)
      window.location.reload();
  }

  function HandleConnectionOffline() {
    setWebOnline(navigator.onLine)
  }

  useEffect(()=> {
    window.addEventListener('online', HandleConnectionOnline)
    window.addEventListener('offline', HandleConnectionOffline)
  }, [])

  return (
    <div className='App'>

      {/* Offline Indicator */}
      {!WebOnline && (
          <OfflineIndicator />
      )}

      <DarkModeProvider> {/* Darkmode context */}
        <SavedProvider> {/* Save Props menu context*/}
          <FeedbackProvider> {/* Feedback context*/}
            <BrowserRouter>
              <Routes>
                <Route index element={<Home />} />
                <Route path='/Home' element={<Home />} />
                <Route path='/Settings' element={<Setting />} />

                {/* Bottom nav routes */}
                <Route path='/Explore' element={<ExploreComp />} />
                <Route path='/Saved' element={<SavedNav />} />
                <Route path='/Notes' element={<Setting />} />

                {/* Error 404 */}
                <Route path='*' element={<Error404 />} />
              </Routes>
            </BrowserRouter>
          </FeedbackProvider>
        </SavedProvider>
      </DarkModeProvider>
    </div>
  );
}
