import { Slide, Zoom, Fade } from "react-slideshow-image";
import { useContext } from "react";
import "react-slideshow-image/dist/styles.css";

import { DarkModeContext } from "../../../DarkMode/Darkmode";

const ImageSlider = (Props) => {
  const StockImage = [
    { src: "/Image/Banner.png", alt: "Banner 1" },
    { src: "/Image/Desain Javas rabbani.jpg", alt: "Banner 2"},
    { src: "/Image/Desain-Palet-warna-kucing.jpg", alt: "Banner 2"},
  ];
  
  const indicators = () => <div className="indicator"></div>;
  
  function disableContextmenu(image) {
    image.preventDefault();
  }

  const ImageStyle = {
    margin: "0px 16px",
    minWidth: "328px",
    height: "148px",
  }

  // Darkmode Toggle
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  return (
    <>
    <style>
      {`
        /* Todo: Custom Indikator Image slide */
        .indicators {
            margin-top: 8px;
        }

        .indicator {
            transition: 0.3s ease;
            cursor: pointer;
            width: 8px;
            height: 6px;
            text-align: center;
            border-radius: 50px;
            background-color: ${modeToggle ? "#696969" : "#D9D9D9"};
            margin: 0px 2px;
        }

        .indicator.active {
            width: 18px;
            height: 6px;
            /* color: #ff0000; */
            background: ${modeToggle ? "tomato" : "var(--Ground-Black)"};
        }
      `}
    </style>
    <Slide
      indicators={indicators}
      nextArrow={<button style={{ display: "none" }} />}
      prevArrow={<button style={{ display: "none" }} />}
      easing="ease"
      canSwipe="true"
      duration={3500} /* slidesToShow={2} */
      {...Props.HideIndicator}
    >
      {StockImage.map((image) => (
        <div style={ImageStyle}>
          {/* Image width original: 328px */}
          <img key={image.src} src={image.src} alt={image.alt} width="100%" style={{borderRadius: "8px", width: "100%", height: "146px", objectFit: "cover", objectPosition: "top"}} onContextMenu={disableContextmenu} draggable="false"/>
        </div>
      ))}
    </Slide>
    
    </>
  );
};
export default ImageSlider;
