// Animate on scroll Library
import AOS from "aos";
import 'aos/dist/aos.css'
import { useEffect } from "react";
import { Link } from "react-router-dom";

export default function HomeCta(Props) {
  useEffect(()=> {
    AOS.init({duration: 1000})
  },[])

  const CtaStyle = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    color: "black",
    textDecoration: "none",
    fontSize: '12px',
    fontFamily: "Main-Book",
    gap: '8px'
  }

  return (
    <>
    <div className="Home-cta" style={{backgroundColor: Props.DarkMode ? "rgb(41, 41, 41)" : "--Ground-White: #FCFCFC"}}>
      <div className="Wrapper-Home-cta">
        <Link to="/Explore" style={CtaStyle}>
            <div className="Explore-resep" style={{backgroundColor: Props.DarkMode ? 'black' : 'var(--Gray-backColor)'}}>
              <img src="/Image/imgComp/Cta-home/3dsearch.png" alt="" width={"40px"}/>
            </div>
          <p style={{color: Props.DarkMode ? "var(--Ground-White)" : "var(--Ground-Black)" }}>Explor resep</p>
        </Link>

        <Link to="" style={CtaStyle}>
          <div className="Coock-book" style={{backgroundColor: Props.DarkMode ? 'black' : 'var(--Gray-backColor)'}}>
            <img src="/Image/imgComp/Cta-home/3dbook.png" alt="" width={"40px"}/>
          </div>
          <p style={{color: Props.DarkMode ? "var(--Ground-White)" : "var(--Ground-Black)" }}>Cook book</p>
        </Link>

        <Link to='' style={CtaStyle}>
          <div className="Info-gizi" style={{backgroundColor: Props.DarkMode ? 'black' : 'var(--Gray-backColor)'}}>
            <img src="/Image/imgComp/Cta-home/3dinfolightning.png" alt="" width={"40px"}/>
          </div>
          <p style={{color: Props.DarkMode ? "var(--Ground-White)" : "var(--Ground-Black)" }}>Food info</p>
        </Link>
      </div>
    </div>
    
    </>
  );
}
