export default function ContentHomeCategory(Props) {
    const BoxStyle = {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "12px",
        textAlign: "center",
        cursor: "pointer"
    }

    const Title = {
        fontSize: "12px",
        fontFamily: "Main-Bold",
        userSelect: "none"
    }

    // Handle event
    function HandleClick() {
        alert("Berhasil!")
    }
    return (
        <div style={BoxStyle} onClick={HandleClick}>
            <h1 style={Title}>{Props.TitleCategory}</h1>
        </div>
    )
}