import Header from "./Nav-comp/Top-nav/Header";
import BottomNav from "./Nav-comp/Bottom-nav/Bottom-nav";
import HomeCta from "./Main-cta-home/Cta-Home";
import BannerCta from "./Board-Home/Banner-cta";
import NewestRecipeSect from "./Newest-recipe-sect/New-recipe-section";
import ResepPilihan from "./Resep-pilihan/ResepPilihan";
import HomeCategory from "./Category-section/Home-category/HomeCategory";
import RecipeScroll from "./Recipe-scroll/RecipeScroll";
import FeedBackPopup from "../Popup-Component/Feedback-Popup/Feedback";

import LoadingPage from "./Loading-page/Loadcomp";

import { useEffect, useState } from "react";

// Feedback Context
import { useContext } from "react";

// Darkmode Context
import { DarkModeContext } from "../DarkMode/Darkmode";
import "./Nav-comp/Setting-sect/Setting.css";

export default function Home() {
  // Auto scroll top
  useEffect(() => {
    window.scrollTo({
      top: "0px",
    });
  }, []);

  // Loading page
  const [load, setLoad] = useState(false);
  useEffect(() => {
      setTimeout(() => {
        setLoad(true);
      }, 200);
  }, []);

  // Darkmode Toggle
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)
  
  return (
      <div style={{backgroundColor: modeToggle ? '#000000' : 'white'}}>
        {modeToggle ? (
          <Header DarkMode={modeToggle}/>
        ) : (
          <Header />
        )}

      {/* Content loading */}
      {load ? (
        <div>
            {modeToggle ?  (
              <>
                <HomeCta DarkMode={modeToggle} />
                <BannerCta DarkMode={modeToggle} />
                <NewestRecipeSect DarkMode={modeToggle} />
                <ResepPilihan DarkMode={modeToggle}/>
                <HomeCategory />
                {/* Line row */}
                <div style={{ padding: "0px 16px" }}>
                  <div style={{borderTop: "rgb(41, 41, 41) solid 1px", marginTop: "32px", marginBottom: "16px",}} />
                </div>
                <RecipeScroll />
              </>
            ) : (
              <>
                <HomeCta />
                <BannerCta />
                <NewestRecipeSect />
                <ResepPilihan />
                <HomeCategory />
                {/* Line row */}
                <div style={{ padding: "0px 16px" }}>
                  <div style={{borderTop: "rgb(232, 239, 243) solid 1px", marginTop: "32px", marginBottom: "16px",}} />
                </div>
                <RecipeScroll />
              </>
            )}
        </div>
      ) : (
        <LoadingPage DarkMode={modeToggle}/>
      )};

      {modeToggle ? (
        <BottomNav DarkMode={modeToggle} />
      ) : (
        <BottomNav />
      )}

    </div> 
  );
}
