import { Link } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import BottomNav from "../Bottom-nav/Bottom-nav";

import DarkModeProvider from "../../../DarkMode/Darkmode";
import { DarkModeContext } from "../../../DarkMode/Darkmode";

import  "../../Board-Home/Banner-cta.css";

import FeedBackPopup from "../../../Popup-Component/Feedback-Popup/Feedback";
import { FeedBackContext } from "../../../Popup-Component/Feedback-Popup/FeedbackContext";

export default function Setting(Props) {
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)

    const SettingContainer = {
        width: "100%",
        height: "100vh",
        color: modeToggle ? "white" : "black",
        backgroundColor: modeToggle ? "black" : "white",
    }

    // Show popup
    const [openTheme, setOpenTheme] = useState(false)

    useEffect(()=> {
        openTheme ? document.body.style.overflow="hidden" : document.body.style.overflow="auto" 
    })

    const OpenThemePopup = ()=> {
        setOpenTheme((prev)=> !prev)
    }
    console.log("darkmd " + modeToggle)

    // Feedback
    const {FeedBackState, setFeedbackState} = useContext(FeedBackContext)
    console.log(FeedBackState + "FROM FEEDBACK CONTEXT")

    const HandleFeedback = () => {
        setFeedbackState((prev)=> !prev)
    }
    useEffect(()=> {
        FeedBackState ? document.body.style.overflow="hidden" : document.body.style.overflow="auto" 
    }, [FeedBackState])

    const BlackBackgrounds = {
        width: "100%",
        height: "100%",
        backgroundColor: "#00000050",
      }  

    return (
        <>
            {/* FEEDBACK POPUP */}
            {FeedBackState && (
                <FeedBackPopup BlackBackground={<div onClick={HandleFeedback} style={BlackBackgrounds}></div>} ThankPopup/>
            )}


            {openTheme && (
                <div className="Comingsoon-continer">
                    <div className="black-background" onClick={OpenThemePopup} />
                    <div className="main-comingsoon" style={{height: "fit-content"}}>
                        <div>
                            {/* <img src="/image/imgComp/comingnew.png" alt="" width={"100px"}/> */}
                        </div>
                        <div style={{textAlign: "center"}}>
                            <h4 style={{fontFamily: "inter", fontSize: "14px", marginBottom : "2px"}}>Ganti tema aplikasi</h4>
                            <p style={{fontFamily: "inter", fontSize: "10px", opacity: "80%"}}>Sedang dalam mode  
                                {modeToggle ? (" (dark)") :(" (light)")} </p>
                        </div>
                        <div style={{marginTop: "12px"}}>
                            <DarkModeProvider>
                                <div class="toggle-switch">
                                    <button onClick={()=> setModeToggle(false)}>Light</button>
                                    <button onClick={()=> setModeToggle(true)}>Dark</button>
                                </div>
                            </DarkModeProvider>
                        </div>
                    </div>
                </div>
            )}
            
                <div style={SettingContainer}>
                    {modeToggle ? (
                        <nav style={{backgroundColor: "black", borderBottom: '1px solid #292929'}}>
                            <Link to="/Home" style={{textDecoration: "none", fontWeight: "600", color: "white"}}><i class="fa-solid fa-arrow-left-long" style={{marginTop: "4px", marginRight: "18px"}}></i>Settings</Link>
                        </nav>
                    ) : (
                        <nav>
                            <Link to="/Home" style={{textDecoration: "none", fontWeight: "600", color: "black"}}><i class="fa-solid fa-arrow-left-long" style={{marginTop: "4px", marginRight: "18px"}}></i>Settings</Link>
                        </nav>
                    )}
                <div className="SettingContainer">
                    <div className="darkmode">
                        <div onClick={OpenThemePopup} style={{display: "flex", flexDirection: "row", justifyContent: "space-between"}}>
                            <p style={{fontWeight: "500", fontFamily: "inter", fontSize: "14px"}}>
                                {modeToggle ? (
                                    <i class="fa-solid fa-circle-half-stroke" style={{color: "white", marginRight: "8px", fontSize: "14px"}}></i>) : (<i class="fa-solid fa-circle-half-stroke" style={{color: "black", marginRight: "8px", fontSize: "14px"}}></i>)}App Theme
                            </p>
                                {modeToggle ? (
                                    <img src="/Icons/Right_Arrow_Darkmode.svg" alt="RightArrow" />
                                ) : (
                                    <img src="/Icons/Right_Arrow.svg" alt="RightArrow" />
                                )}
                        </div>

                        {/* Paragraf legend for feedback */}
                        <p style={{marginTop: "0px", fontSize: "12px", color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)", }}>Beri masukkan dan saran</p>
                        
                        {/* Feedback section */}
                        <div style={{display: "flex", flexDirection: "row", justifyContent: "space-between"}} onClick={HandleFeedback}>
                            <p style={{fontWeight: "500", fontFamily: "inter", fontSize: "14px"}}>
                                {modeToggle ? (
                                <i class="fa-solid fa-message" style={{color: "white", marginRight: "8px", fontSize: "14px"}}></i>) : (<i class="fa-solid fa-message" style={{color: "black", marginRight: "8px", fontSize: "14px"}}></i>)}Feedback
                            </p>
                                {modeToggle ? (
                                    <img src="/Icons/Right_Arrow_Darkmode.svg" alt="RightArrow" />
                                ) : (
                                    <img src="/Icons/Right_Arrow.svg" alt="RightArrow" />
                                )}
                        </div>

                        {/* <div style={{textAlign: ""}}>
                            <p style={{marginTop: "0px", fontSize: "12px", color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)" ,}}>App version 0.1.0</p>
                        </div> */}
                    </div>
                </div>

                

                {modeToggle ? (
                    <BottomNav DarkMode={modeToggle} />
                ) : (
                    <BottomNav />
                )}
            </div>
        </>
    )
}