import { useContext, useEffect, useState } from "react";
import { Link, useNavigate, NavLink, useLocation } from "react-router-dom";

export default function BottomNav(Props) {

    // Fungsi disable Contextmenu
    function DisableHoldContextmenu(Section) {
        window.addEventListener('contextmenu', () => {
            Section.preventDefault();
        })
    }

    const [homeIcon, setHomeIcon] = useState(true)
    const [searchIcon, setSearchIcon] = useState(false)
    const [saveIcon, setSaveIcon] = useState(false)
    const [noteIcon, setNoteIcon] = useState(false)

    const location = useLocation();

    useEffect(() => {
      // Perbarui ikon berdasarkan path URL saat ini
      if (location.pathname === "/Home") {
        setHomeIcon(true);
        setSearchIcon(false);
        setSaveIcon(false)
        setNoteIcon(false)
      } else if (location.pathname === "/Explore") {
        setSearchIcon(true);
        setHomeIcon(false);
        setSaveIcon(false)
        setNoteIcon(false)
      } else if (location.pathname === "/Saved") {
        setSaveIcon(true);
        setHomeIcon(false);
        setSearchIcon(false);
        setNoteIcon(false)
      } else if (location.pathname === "/Note") {
        setNoteIcon(true)
        setSaveIcon(false);
        setHomeIcon(false);
        setSearchIcon(false);
      }
    }, [location.pathname]);

    // Darkmode style area
    const BottomNavDarkMode = {
      backgroundColor: Props.DarkMode ? "#000000" :  "var(--Ground-White)",
      borderTop: Props.DarkMode ? "1px solid #292929" : "1px solid var(--Line-universal)"
    }

    const ParagDarkMoodeHome = {
      fontWeight: homeIcon ? "800" : "600",
      color: Props.DarkMode ? "#999999" : "var(--Accent-color-nav)"
    }
    const ParagDarkMoodeSearch = {
      fontWeight: searchIcon ? "800" : "600",
      color: Props.DarkMode ? "#999999" : "var(--Accent-color-nav)"
    }
    const ParagDarkMoodeSaved = {
      fontWeight: saveIcon ? "800" : "600",
      color: Props.DarkMode ? "#999999" : "var(--Accent-color-nav)"
    }
    const ParagDarkMoodeNote = {
      fontWeight: noteIcon ? "800" : "600",
      color: Props.DarkMode ? "#999999" : "var(--Accent-color-nav)"
    }
  return (
    <div className="Bottom-nav" style={BottomNavDarkMode}>
      <div className="Wrapper-bottom-nav">
        {/* Home Active (default) nav */}
        <NavLink to={'/Home'} style={{textDecoration: "none"}} className="Bottom-nav-HomeCta" onContextMenu={DisableHoldContextmenu}>
            {homeIcon ? (Props.DarkMode ? (<img src="/Icons/nav-icon/Darkmode/home-out-darkmode.png" alt="Home-ic" draggable="false" />) : (<img src="/Icons/nav-icon/home-out.svg" alt="Home-ic" draggable="false" />)) 
            
            
            : (<img src="/Icons/nav-icon/home.svg" alt="Home-ic" draggable="false" />)}
            <p style={ParagDarkMoodeHome}>Recipes</p>
        </NavLink>

        {/* Search nav */}
        <NavLink to={'/Explore'} style={{textDecoration: "none"}} className="Bottom-nav-SearchCta" onContextMenu={DisableHoldContextmenu}>
            {searchIcon ? (Props.DarkMode ? (<img src="/Icons/nav-icon/Darkmode/Search-out-darkmode.svg" alt="Search-ic" draggable="false" />) : (<img src="/Icons/nav-icon/Search-out.svg" alt="Search-ic" draggable="false" />)) : (<img src="/Icons/nav-icon/Search.svg" alt="Search-ic" draggable="false" />)}
            <p style={ParagDarkMoodeSearch}>Explore</p>
        </NavLink>

        {/* Save Nav */}
        <NavLink to={"/Saved"} style={{textDecoration: "none"}} className="Bottom-nav-SavedCta" onContextMenu={DisableHoldContextmenu}>
          {saveIcon ? (Props.DarkMode ? (<img src="/Icons/nav-icon/Darkmode/Heart-out-darkmode.svg" alt="" />) : (<img src="/Icons/nav-icon/Heart-out.svg" alt="Save-ic" draggable="false" />)) : (<img src="/Icons/nav-icon/Heart.svg" alt="Save-ic" draggable="false" />)}
          <p style={ParagDarkMoodeSaved}>SAVED</p>
        </NavLink>

        {/* Note Nav */}
        <NavLink to={"/Note"} style={{textDecoration: "none"}} className="Bottom-nav-NoteCta" onContextMenu={DisableHoldContextmenu}>
          {noteIcon ? (Props.DarkMode ? (<i class="fa-solid fa-square-plus" style={{color: "#999999"}}></i>) : (<i class="fa-solid fa-square-plus"></i>)) : (Props.DarkMode ? (<i class="fa-regular fa-square-plus"></i>) : (<i class="fa-regular fa-square-plus" style={{color: "#999999"}}></i>))}
          <p style={ParagDarkMoodeNote}>NOTE</p>
        </NavLink>

      </div>
    </div>
  )
}
