import { useEffect, useState } from "react";
import AOS from "aos";
import ContentHLongRP from "./Content-long/Content-H-Long";

// Poup style
import '../Board-Home/Banner-cta.css'

// darkmode
import { useContext } from "react";
import { DarkModeContext } from "../../DarkMode/Darkmode";

export default function ResepPilihan(Props) {
  useEffect(()=> {
    AOS.init({duration: 1000})
  })

  // Popup
  const [comingPopup, setComingPopup] = useState()
  const HandleShow = () => {
    setComingPopup(!setComingPopup)
  }

  useEffect(()=> {
    comingPopup ? document.body.style.overflow="hidden" : document.body.style.overflow="auto";
  }, [comingPopup])

  // darkmode
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  return (
    <>
      {comingPopup && (
        <div className="Comingsoon-continer">
          <div className="black-background" onClick={HandleShow} />
            <div className="main-comingsoon">
              <div style={{marginBottom: "8px"}}>
                <img src="/image/imgComp/comingnew.png" alt="" width={"112px"}/>
              </div>
              <div style={{textAlign: "center"}}>
                  <h4 style={{fontFamily: "inter", fontSize: "14px", marginBottom : "2px"}}>In development stage...</h4>
                  <p style={{fontFamily: "inter", fontSize: "12px", opacity: "80%"}}>Akan segera tersedia!</p>
              </div>
              <div style={{marginTop: '16px'}}>
                <button className="button-coming" onClick={HandleShow}>okay</button>
              </div>
          </div>
        </div>
    )}
      <div className="Long-card-pilihan" 
      // data-aos="fade-up"
      >
        {/* Top Section */}
        <div className="Top-pilihan-sect">
          {/* Title section */}
          <h1 style={{color: Props.DarkMode ? "white" : "black"}}>Resep pilihan hari ini</h1>
          {/* Button Section */}
          <div>
            <button onClick={() => setComingPopup(true)}>Lihat semua</button>
          </div>
        </div>

        <div className="Content-long-RP">
          <div className="Wrapper-content-long-RP">
            <div>
              {modeToggle ? (
                <ContentHLongRP
                  DarkMode={modeToggle}
                  V2Src="/Image/Food-img/image 2img1.jpg"
                  Judul="Ayam goreng"
                  Desc="Yang digoreng dengan bumbu- bumbu yang khas."
                />
              ) : (
                <ContentHLongRP
                  V2Src="/Image/Food-img/image 2img1.jpg"
                  Judul="Ayam goreng"
                  Desc="Yang digoreng dengan bumbu- bumbu yang khas."
                />
              )}
            </div>
            <div>
              {modeToggle ? (
                <ContentHLongRP
                  DarkMode={modeToggle}
                  V2Src="/Image/Food-img/Image 3.jpg"
                  Judul="Ikan bakar"
                  Desc="Bahan makanan yang mudah dan simpel, mudah"
                />
              ) : (
                <ContentHLongRP
                  V2Src="/Image/Food-img/Image 3.jpg"
                  Judul="Ikan bakar"
                  Desc="Bahan makanan yang mudah dan simpel, mudah"
                />
              )}
            </div>
            <div>
              {modeToggle ? (
                <ContentHLongRP
                  DarkMode={modeToggle}
                  V2Src="/Image/Food-img/image 2img1.jpg"
                  Judul="Bagas guling"
                  Desc="Menggunakan bahan dan langkah yang tidak sulit"
                />
              ) : (
                <ContentHLongRP
                  V2Src="/Image/Food-img/image 2img1.jpg"
                  Judul="Bagas guling"
                  Desc="Menggunakan bahan dan langkah yang tidak sulit"
                />
              )}
            </div>
            <div>
              {modeToggle ? (
                <ContentHLongRP
                  DarkMode={modeToggle}
                  V2Src="/Image/Food-img/image 2img1.jpg"
                  Judul="Bagas guling"
                  Desc="Menggunakan bahan dan langkah yang tidak sulit"
                />
              ) : (
                <ContentHLongRP
                  V2Src="/Image/Food-img/image 2img1.jpg"
                  Judul="Bagas guling"
                  Desc="Menggunakan bahan dan langkah yang tidak sulit"
                />
              )}
            </div>
          </div>
        </div>
      </div>
    
    
    </>
  );
}
