import { useEffect } from "react";
import RecipeScrollContent from "./RecipeScrollContent";
import AOS from "aos";
import { Link } from "react-router-dom";

// Darkmode
import { DarkModeContext } from "../../DarkMode/Darkmode";
import { useContext } from "react";

export default function RecipeScroll(Props) {
    useEffect(()=> {
        AOS.init({duration: 1000})
    })

    // Darkmode
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  return (
    <>
      <style>
        {`
            .RecipeScroll {
                min-width: 328px;
                height: 270px;
            }

            .Wrapper-recipeScroll {
                width: 100%;
                height: 100%;

                display: "flex";
                flex-direction: "column";
                gap: "8px";
            }

            `}
      </style>
      <div className="RecipeScroll" 
    //   data-aos="fade-up" 
      data-aos-delay="1100"
      style={{...Props.PropsHeight}}>
        
        <div className="Wrapper-recipeScroll">
            <div>
              {modeToggle ? (
                <RecipeScrollContent DarkMode={modeToggle} Title="Sayur sop" Desc="yang digoreng dengan bumbu- bumbu yang khas." Bahan="7 Bahan" TimeCook="28 Menit"/>
              ) : (
                <RecipeScrollContent Title="Sayur sop" Desc="yang digoreng dengan bumbu- bumbu yang khas." Bahan="7 Bahan" TimeCook="28 Menit"/>
              )}
            </div>
            <div>
              {modeToggle ? (
                <RecipeScrollContent DarkMode={modeToggle} Title="Sayur sop" Desc="yang digoreng dengan bumbu- bumbu yang khas." Bahan="7 Bahan" TimeCook="28 Menit"/>
              ) : (
                <RecipeScrollContent Title="Sayur sop" Desc="yang digoreng dengan bumbu- bumbu yang khas." Bahan="7 Bahan" TimeCook="28 Menit"/>
              )}
            </div>
            <Link to="/Explore" style={{display: "grid", placeItems: "center", marginTop: "16px", fontSize: "12px", fontFamily: "Main-Bold", alignItems: "center", color: "tomato", textDecoration: "none", ...Props.DisableViewMore}}>
                <p>View more</p>
            </Link>
        </div>
      </div>

    </>
  );
}
