import { useEffect, useRef, useState, useContext } from "react";
import SavedNav from "../../SavedSect";
import { SavedContext } from "../ContextProps";

import { DarkModeContext } from "../../../DarkMode/Darkmode";

export default function ContentNewSect({ children, ...Props }) {
  const [isOpen, setOpen] = useState(false);
  const HandleClickOpen = () => {
    setOpen(true);
  };

  useEffect(() => {
    document.body.style.overflow = isOpen ? "hidden" : "auto";
  }, [isOpen]);

  const PropsStyle = {
    width: "120px",
    height: "fit-content",
    display: "flex",
    flexDirection: "column",
    borderRadius: "8px",
    backgroundColor: "transparent",
    outline: Props.DarkMode ? "1px solid #292929" : "1px solid #E8EFF3",
  };

  const TitleStyle = {
    display: "flex",
    flexDirection: "row",
    gap: "2px",
    alignItems: "center",
  };

  const SubtitleCardStyle = {
    color: Props.DarkMode ? "var(--Inner-Input)" : "#4D4D4D",
    fontSize: "10px",
    fontFamily: "inter",
    fontWeight: "400",
  };
  const BackgroundBlack = {
    width: "100%",
    height: "100%",
    position: "fixed",
    backgroundColor: "#00000050",
    opacity: isOpen ? "1" : "0",
    zIndex: isOpen ? "10" : "-10",
    top: "0px",
    left: "0px",
    transition: "0.3s",
  };

  function HandleContextmenu(image) {
    image.preventDefault();
  }

  const CloseOpenedProps = useRef();
  const [closeProps, setCloseProps] = useState(false);

  useEffect(() => {
    if (closeProps) {
      CloseOpenedProps.current.style.bottom = "-500px";
      CloseOpenedProps.current.style.transition = "0.7s ease";
      setTimeout(() => {
        setCloseProps(false);
      }, 700);
    }
  }, [closeProps]);

  const { solid, setSolidSave } = useContext(SavedContext);
  const handleSolidSave = () => {
    setSolidSave((prev) => !prev);
  };

  const HandleNotif =  
      <div className="notif-saved">
        <p>Menu tersimpan</p>
      </div>
  

  const [Notif, setNotif] = useState(HandleNotif)

  // Darkmode Toggle
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  return (
    <>
      {isOpen && (
        <div
          data-aos="slide-up"
          className="PropsOpen"
          ref={CloseOpenedProps}
          style={{ left: "0px", backgroundColor: Props.DarkMode ? "var(--Ground-Black)" : "white" }}
        >
          <div className="Wrapper-propsOpen">
            <div className="Image-props">
              <div
                className="HandleContainer"
                onClick={() => {
                  setCloseProps(true);
                  setTimeout(() => {
                    setOpen(false);
                  }, 700);
                }}
                onTouchStart={() => {
                  setCloseProps(true);
                  setTimeout(() => {
                    setOpen(false);
                  }, 700);
                }}
              >
                <div className="HandleToggle" style={{backgroundColor: Props.DarkMode ? "#696969" : "#8B8B8B99"}} />
              </div>
              <img
                src={Props.Src}
                alt={Props.Alt}
                draggable="false"
                onContextMenu={HandleContextmenu}
                style={{
                  width: "100%",
                  height: "200px",
                  marginTop: "0px",
                  objectFit: "cover",
                  borderRadius: "17px",
                }}
              />
            </div>
            <div className="info-props">
              <h1 style={{color: Props.DarkMode ? "white" : "black"}}>{Props.Title}</h1>
              <p style={{color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>{Props.Desc}</p>
            </div>
            <div className="InfoStep">
              <div>
                <div style={{ marginRight: "14px" }}>
                  <i className="fa-regular fa-clock" style={{ fontSize: "12px", color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}></i>
                  <p style={{color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>{Props.waktuMasak}</p>
                </div>
                <div>
                  {modeToggle ? (
                    <img src="/Icons/Document-Darkmode.svg" alt="Doc-ic" style={{ marginLeft: "-2px" }} />
                  ) : (
                    <img src="/Icons/Document.svg" alt="Doc-ic" style={{ marginLeft: "-2px" }} />
                  )}
                  <p style={{color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>{Props.Bahan}</p>
                </div>
              </div>
            </div>
            <div className="Cta-btn-props">
              <div className="Wrapper-cta-btn-props">
                <div className="Start-coock-props">
                  <p>Mulai memasak</p>
                </div>
                <div className="Save-coock-props" onClick={handleSolidSave} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {solid ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {solid && (
          {Notif}
      )}

      <div
        style={BackgroundBlack}
        onClick={() => {
          setCloseProps(true);
          setTimeout(() => {
            setOpen(false);
          }, 500);
        }}
      ></div>
      <div onClick={HandleClickOpen}>
        <div style={PropsStyle}>
          <div>
            <img
              src={Props.Src}
              alt={Props.Alt}
              draggable="false"
              onContextMenu={HandleContextmenu}
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "8px 8px 0px 0px",
                objectFit: "cover",
              }}
            />
          </div>
          <div style={{ padding: "8px 5px" }}>
            <h1 style={{ fontSize: "14px", color: Props.DarkMode ? "white" : "black" }}>{Props.Title}</h1>
            <div style={TitleStyle}>
              {modeToggle ? (
                <img src="/Icons/Document-Darkmode.svg" alt="Doc-ic" style={{ marginLeft: "-2px" }} />
              ) : (
                <img src="/Icons/Document.svg" alt="Doc-ic" style={{ marginLeft: "-2px" }} />
              )}
              <p style={SubtitleCardStyle}>{Props.Bahan}</p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
