import { useState } from "react";

export default function ContentHLongRP(PropsCL) {
    // Disabla context menu
    function DisableContextMenu(userHold) {
        userHold.preventDefault();
    }

    const StylingProps = {
        width: "155px",
        height: "fit-Content",
        borderRadius: "8px",
        fontFamily: "Main-Bold"
    }

    const [openHlong, setOpenLong] = useState(false);
    function HandleHLongClick() {
        setOpenLong(true)
    }

    return (
        <>
            <div style={StylingProps} onClick={HandleHLongClick}>
                <img src={PropsCL.V2Src} alt="" style={{width: "100%", height: "240px", borderRadius: "8px", objectFit: "cover"}} onContextMenu={DisableContextMenu} draggable="false" />

                {/* Title n desc props */}
                <div>
                    <h1 style={{fontSize: "14px", margin: "4px 0px", color: PropsCL.DarkMode ? "white" : "black"}}>{PropsCL.Judul}</h1>
                    <p style={{fontSize: "10px", fontFamily: "Inter", fontWeight: "400", color: PropsCL.DarkMode ? "var(--Inner-Input)" : "black"}}>{PropsCL.Desc}</p>
                </div>
            </div>

            {openHlong && (
                alert("Berhasil")
            )}
        
        </>
    )
}