import { useEffect, useRef, useState } from "react";
import MOCKDATA from "../../../Assets/MOCK_DATA.json";
import { Link} from "react-router-dom";

import { createContext } from "react";
import { to } from "react-spring";


// import AOS from "aos";
// import "aos/dist/aos.css";

// import Skeleton from "react-loading-skeleton";
// import "react-loading-skeleton/dist/skeleton.css";

export const RefStateSearch = createContext()

export default function Header(Props) {
  const [getData, setData] = useState([]);
  const [ShowOutput, SetOutput] = useState();

  const HandleChange = (e) => {
    const Filtered = e.target.value;
    const ReadyFilter = MOCKDATA.filter(
      (item) =>
        item.first_name.toLowerCase().includes(Filtered) ||
        item.last_name.toLowerCase().includes(Filtered)
    );

    ReadyFilter.sort((a, b) => {
      const nameA = a.first_name.toLowerCase();
      const nameB = b.first_name.toLowerCase();
      if (nameA < nameB) return -1; 
      if (nameA > nameB) return 1; 
      return 0; // 0 = Jika Alfabet sama
    });

    setData(ReadyFilter);

    if (Filtered) {
      SetOutput(true);
    } else {
      SetOutput(false);
    }
  };

  const ValueInputRef = useRef();
  // X-mark input = Handle clear value
  const HandleClearInput = () => {
    ValueInputRef.current.value= null;
    SetOutput(false)
  }

  function DirectPage(Param) {
    <Link to="/Explore">
      {Param}
    </Link>
  }

  const newLinkStyle = {
    textDecoration: "none",
    ...Props.NewWidth,
  }

  // Darkmode style area
  const HeaderDarkMode = {
    backgroundColor: Props.DarkMode ? "#000000" : 'var(--Ground-Black)',
    borderBottom: Props.DarkMode ? '1px solid #292929' : '0px',
  }

  const SearchBoxDarkMode = {
    ...Props.NewSearchBox,
    backgroundColor: 'var(--Shade-box)',
    color: " var(--Inner-Input)"

  }

  const SettingDarkMode = {
    ...Props.HiddenSetting,
    color: "var(--Inner-Input)",

  }

  return (
    <div>
      <div className="Header" style={Props.NewHeight}>
        <div className="Wrapper-H" style={HeaderDarkMode}>
          {/* Search Section */}

          <Link to="/Explore" style={newLinkStyle} >
            <div className="Search-Box" style={SearchBoxDarkMode}>
              <i class="fa-solid fa-magnifying-glass"></i>
              <input
                inputMode="search"
                type="Text"
                placeholder="Ingin masak apa hari ini?"
                onChange={HandleChange}
                ref={ValueInputRef}
              />
              <div className="X-mark" style={{display: ShowOutput ? "flex" : "none" }} onClick={HandleClearInput}>
                <i class="fa-solid fa-xmark"></i>
              </div>
            </div>
          </Link>
          {/* Setting Section */}
          <Link to="/Settings" className="Setting-Section" style={SettingDarkMode}>
            <i class="fa-solid fa-gear" style={{ fontSize: "18px" }}></i>
          </Link>
        </div>

        <div></div>
      </div>
      {/* Output Input */}
      <div
        className="Output-Input"
        style={{ height: ShowOutput ? "200px" : "0px" }}
      >
        {getData.length > 0 ? (
          getData.map((item, index) => (
            <div
              className="Wrapper-output-input"
              style={{ display: ShowOutput ? "flex" : "none" }}
            >
              <p>
                {index + 1 + `. `}
                {item.first_name} {item.last_name}
              </p>
            </div>
          ))
        ) : (
          <p style={{ padding: "4px 24px", opacity: "50%", fontSize: "12px" }}>
            Resep tidak ditemukan.
          </p>
        )}

        {(getData.value = "" ? <p>Tidak ada data</p> : <div></div>)}
      </div>
    </div>
  );
}