export default function OfflineIndicator() {
  return (
    <div>
        <div className="Comingsoon-continer">
          <div className="black-background" style={{backgroundColor: "#00000090"}}/>
          <div className="main-comingsoon">
            <div style={{ marginBottom: "16px" }}>
              <img src="/image/Offline-indicator/offline-crton.png" alt="" width={"100px"} />
            </div>
            <div style={{ textAlign: "center" }}>
              <h4 style={{fontFamily: "inter", fontSize: "14px",marginBottom: "2px",}}>Internet connection required!</h4>
              <p style={{fontFamily: "inter", fontSize: "12px", opacity: "80%",}}>Turn on internet to open</p>
            </div>
          </div>
        </div>
    </div>
  );
}
